// Creating own less file because directly in tsx it's not possible: https://github.com/webpack-contrib/mini-css-extract-plugin/issues/73
@import "~antd/lib/style/themes/default";
@import "~antd/lib/style/core/motion";
@import "~antd/lib/form/style/index";
@import "~antd/lib/input/style/index";
@import "~antd/lib/checkbox/style/index";
@import "~antd/lib/tooltip/style/index";
@import "~antd/lib/alert/style/index";
@import "~antd/lib/spin/style/index";
@import "~antd/lib/message/style/index";
@import "~antd/lib/modal/style/index";
@import "~antd/lib/button/style/index";
@import "~antd/lib/select/style/index";
@import "~antd/lib/divider/style/index";
@import "~antd/lib/list/style/index";
@import "~antd/lib/space/style/index";
@import "~antd/lib/radio/style/index";

.rpm-wpc-antd-list-item-meta-title {
    margin-top: 0 !important;
}

// Fix select inputs in WordPress backend
.rpm-wpc-antd-select-single:not(.rpm-wpc-antd-select-customize-input) .rpm-wpc-antd-select-selector {
    box-sizing: border-box;
}

.rpm-wpc-antd-select-item {
    line-height: 2;
}

.rpm-wpc-antd-radio-inner::after {
    top: 4px;
    left: 4px;
}

// Do not show validate icon in any form, causes issues with input form groups (installation type, license key)
.rpm-wpc-antd-form-item-children-icon {
    display: none;
}

// Fix modal issues with admin bar in plugins list
body.rpm-wpc-antd-modal-open {
    #wpadminbar,
    #adminmenuwrap {
        z-index: 999;
    }
}

// Fix select dropdowns and messages in modals
.rpm-wpc-antd-select-dropdown, .rpm-wpc-antd-message {
    z-index: 105000000;
}

.rpm-wpc-antd-checkbox-inner::after {
    margin-top: -1px;
}

// The hover animation is broken on most WordPress themes, we need to deactivate it
.rpm-wpc-antd-checkbox-wrapper:hover .rpm-wpc-antd-checkbox::after,
.rpm-wpc-antd-checkbox:hover::after {
    visibility: hidden !important;
}

@ant-prefix: rpm-wpc-antd;